import { useState, useEffect } from 'react'
import UnderlineText from '../../../components/UnderlineText/UnderlineText'
import DeriNumberFormat from "../../../utils/DeriNumberFormat"
import { isCakeLP, isSushiLP } from "../../../utils/utils"
import Icon from '../../../components/Icon/Icon'
import Search from '../../../components/Search/Search'
import errorimg from '../../../assets/img/ErrorImg.png'
import stakedLogo from '../img/staked.svg'
export default function TokenList({  lang, address, checkoutBaseToken, nowToken, bTokenList, version, type }) {
  const [showBTokenList, setShowBTokenList] = useState([])
  const onImgError = (event) => {
    event.target.src = errorimg
  }
  useEffect(() => {
    setShowBTokenList(bTokenList)
  }, [bTokenList])
  return (
    <div className='select-basetoken-info'>
      <div className='select-coin-title-search'>
        <div className='select-coin-title'> {lang['select-the-coin']}</div>
        <div className='search-box'>
          <Search list={bTokenList} searchName='bTokenSymbol' onChange={(lsit) => setShowBTokenList(lsit)} />
        </div>
      </div>
      <div className='coin-search'>
        <div className='slide-icon left'>
          <Icon token='arrow-left' width='24' height='24' />
        </div>
        <div className={(version === 'v3' || version === 'v3_lite') ? "coin-list some-coin" : "coin-list"}>
          {type !== 'lp' && version !== 'v2_lite_open' && <>
            {showBTokenList.map((item, index) =>
            (<div className={nowToken === item.bTokenSymbol ? 'coin-box checked-base' : 'coin-box'} onClick={() => { checkoutBaseToken(item.bTokenId, item.bTokenSymbol) }}>
              <div className='base-apy-info'>
                <div className={`btoken-logo ${item.bTokenSymbol}`}></div>
                <div className='apy-base-staked'>
                  <div className='base-name'>{item.bTokenSymbol}</div>
                  <div className='base-apy'>
                    {lang['apy']} &nbsp; <DeriNumberFormat width='50px' value={item.apy} suffix='%' displayType='text' allowZero={true} decimalScale={2} />
                    {(version === 'v3' || version === 'v3_lite') && <>
                      <span> +  <DeriNumberFormat width='50px' value={item.supplyApy} suffix='%' displayType='text' allowZero={true} decimalScale={2} />  + </span>
                      <DeriNumberFormat width='50px' value={item.xvsApy} suffix='%' displayType='text' allowZero={true} decimalScale={2} />
                    </>}
                  </div>
                </div>
              </div>
              {item.staked && <img src={stakedLogo} alt='' />}
            </div>)
            )}
          </>}
          {version === 'v2_lite_open' && <>
            {showBTokenList.map((item, index) =>
            (<div className='coin-box checked-base' onClick={() => { checkoutBaseToken(item.bTokenId) }}>
              <div className='base-apy-info'>
                <div className={`btoken-logo ${item.bTokenSymbol}`}>
                  <img alt='' onError={onImgError} src={`https://raw.githubusercontent.com/deri-finance/deri-open-zone/main/img/${item.bTokenSymbol}.png`} />
                </div>
                <div className='apy-base-staked'>
                  <div className='base-name'>{item.bTokenSymbol}</div>
                </div>
              </div>
              {item.staked && <div className='stake'>
                <img src={stakedLogo} alt='' />
                {lang['staked']}
              </div>}
            </div>)
            )}
          </>}
          {type === 'lp' && bTokenList.length && <>
            <div className='coin-box checked-base'>
              <div className='base-apy-info'>
                <div className='lp-btoken-logo'>
                  <div className={`btoken-logo ${bTokenList[0].bTokenSymbol}`}></div>
                  <span className='conect-logo'>+</span>
                  <div className={`btoken-logo ${bTokenList[1].bTokenSymbol}`}></div>
                </div>
                <div className='apy-base-staked'>
                  <div className='base-name'>{nowToken}</div>
                  <div className='base-apy'>
                    {isCakeLP(address) && <>
                      {lang['apy']} &nbsp; <DeriNumberFormat value={bTokenList[1].apy} suffix='%' displayType='text' allowZero={true} decimalScale={2} />
                    </>}
                    {isSushiLP(address) && <>
                      <UnderlineText text={`${lang['apy']} ${(+(bTokenList[1].apy)).toFixed(2)} %`} tip={lang['deri-apy']} />
            &nbsp; + &nbsp; <UnderlineText text={`${lang['apy']}  ${(+(bTokenList[1].lpApy)).toFixed(2)} %`} tip={lang['sushi-apy']} />
                    </>}
                  </div>
                </div>
              </div>
            </div>
          </>}
        </div>
        <div className='slide-icon right'>
          <Icon token='arrow-right' width='24' height='24' />
        </div>
      </div>
    </div>
  )
}