import { useEffect, useState } from 'react'
import apiProxy from '../../model/ApiProxy'
import styled from 'styled-components'
import Icon from '../Icon/Icon';
import UnderlineText from '../UnderlineText/UnderlineText'

const Wrapper = styled.div`
  .remove-hint{
    cursor: pointer;
    .text{
      display: flex;
      border:0;
    }
    span{
      margin-left:8px;
    }
  }
`

export default function RemoveHint({lang, baseToken, penalty, isB0Staked, availableBalance,getLang }) {
  const [text,setText] = useState('')
  useEffect(()=>{
    let label;
    let text = {num:baseToken}
    if(isB0Staked){
      if(baseToken === 'BUSD'){
        label = `${lang['your-account-has-an-unsettled-balance-of']} ${penalty} BUSD ${lang['this-action-will-update-your']}`
      } else {
        label = `${lang['your-account-has-an-unsettled-balance-of']} ${penalty} BUSD ${getLang('this-action-will-trigger-swapping-to-update-your',text)}`
      }
    } else{
      label = `${lang['your-account-has-an-unsettled-balance-of']} ${penalty} BUSD ${getLang('this-action-will-trigger-swapping-to-update-your-busd-balance-to-reflect-the-unsettled-amount',text)}`
    }
    setText(label)
  },[isB0Staked,penalty,baseToken])
  return (
    <Wrapper>
      {(+penalty) < 0 &&  (+availableBalance) > 0 &&  <div className='remove-hint'>
          <UnderlineText tip={text} width='332'>
            <Icon token='warn' /> <span>{lang['negative-unsettled-balance']}</span>
          </UnderlineText>
        </div>}
    </Wrapper>

  )
}