/* eslint-disable react/jsx-no-target-blank */
import { useState, useEffect } from 'react'
import { Link, useParams } from 'react-router-dom'
import { inject, observer } from 'mobx-react'
import Claim from './Claim'
import OperateLiquidity from './Detail/OperateLiquidity'
import Tab from '../../components/Tab/Tab'
import apiProxy from "../../model/ApiProxy"
import { isCakeLP, isSushiLP } from "../../utils/utils"
import config from '../../config.json'
import useConfig from '../../hooks/useConfig'
import { DeriEnv, openConfigListCache } from '../../lib/web3js/index'
import './detail.less'
import backIcon from './img/back.svg'
import BSC from '../../assets/img/bsc-logo.svg'
import POLYGON from '../../assets/img/polygon-logo.svg'
import ETH from '../../assets/img/ethereum.svg'
import stakedLogo from './img/staked.svg'
import errorimg from '../../assets/img/ErrorImg.png'
import addicon from './img/add-icon.svg'
import removeicon from './img/remove-icon.svg'
import inaddicon from './img/inactive-add.svg'
import inremoveicon from './img/inactive-remove.svg'
import PoolInfo from './Detail/PoolInfo'
import TokenList from './Detail/TokenList'

const env = DeriEnv.get();
const { chainInfo } = config[env]
function Detail({ lang, wallet = {}, poolManager,getLang }) {
  const { version, chainId, address, type } = useParams();
  const [nowToken, setNowToken] = useState('')
  const [nowTokenId, setNowTokenId] = useState('')
  const [nowIsStaked, setNowIsStaked] = useState(false);
  const [poolConfig, setPoolConfig] = useState('')
  const [bTokenList, setBTokenList] = useState([])
  const [poolInfo, setPoolInfo] = useState({})
  const config = useConfig();
  const [symbols, setSymbols] = useState('')
  const [walletBalance, setWalletBalance] = useState()
  const [nowApy, setNowApy] = useState({})
  const networkText = chainInfo[chainId] && chainInfo[chainId].name;

  const networkLogo = (chainId) => {
    let src;
    let id = +chainId
    if (id === 56 || id === 97) {
      src = BSC
    } else if (id === 137) {
      src = POLYGON
    } else if (id === 1) {
      src = ETH
    }
    return src
  }
  const onImgError = (event) => {
    event.target.src = errorimg
  }
  const getWalletBalance = async () => {
    if (wallet.isConnected()) {
      let params = [wallet.detail.chainId, address, wallet.detail.account]
      let balance = null;
      if (poolConfig.isLp) {
        balance = await apiProxy.request('getLpWalletBalance', params)
      } else {
        if (version === 'v3' || version === 'v3_lite') {
          params.push(nowToken)
        } else {
          params.push(nowTokenId)

        }
        if (version === 'v2_lite_open') {
          await openConfigListCache.update()
        }
        balance = await apiProxy.request('getBTokenBalance', params)
      }
      setWalletBalance(balance)
    }
  }
  const afterTransaction = () => {
    getWalletBalance()
    getLiquidityInfo()
    getIsStaked()
  }
  const tabSet = {
    [lang['add']]: {
      content: <OperateLiquidity getLang={getLang} poolConfig={poolConfig} isLp={type} wallet={wallet} afterTransaction={afterTransaction} version={version} bTokenId={nowTokenId} address={address} lang={lang} type='add' poolInfo={poolInfo} baseToken={nowToken} walletBalance={walletBalance} />,
      style: { background: "linear-gradient(200.74deg, rgba(97, 122, 255, 0.1) -4.65%, rgba(223, 229, 255, 0) 50.94%, rgba(85, 91, 237, 0.05) 105.4%)" },
      borderColor: "#203B60",
      icon: addicon,
      inicon: inaddicon,
      direction: "tab-left"
    },
    [lang['remove']]: {
      content: <OperateLiquidity getLang={getLang} isLp={type} poolConfig={poolConfig} wallet={wallet} afterTransaction={afterTransaction} lang={lang} address={address} type='remove' bTokenId={nowTokenId} poolInfo={poolInfo} balance={poolInfo} baseToken={nowToken} version={version} />,
      style: { background: "linear-gradient(200.74deg, rgba(97, 122, 255, 0.1) -4.65%, rgba(223, 229, 255, 0) 50.94%, rgba(85, 91, 237, 0.05) 105.4%)" },
      borderColor: "#203B60",
      icon: removeicon,
      inicon: inremoveicon,
      direction: "tab-right",
    },
  }
  const load = async () => {
    await poolManager.load()
    let pool = poolManager.pools;
    if (type === 'lp') {
      await poolManager.loadDoubleMingingPool();
      pool = poolManager.lpPools
    }
    if (version === 'v2_lite_open') {
      await poolManager.loadOpen();
      pool = poolManager.openPools
    }
    pool = pool.filter(p => p.address === address)
    pool = pool[0]
    let bToken0 = pool.bTokens[0].bTokenSymbol
    if (isCakeLP(address)) {
      bToken0 = 'PANCAKE-LP'
    } else if (isSushiLP(address)) {
      bToken0 = 'DERI-USDT SLP'
    }
    let bTokenId = pool.bTokens[0].bTokenId
    let symbols = pool.symbols.map(item => {
      let arr = [];
      arr.push(item.symbol)
      return arr
    })
    symbols = symbols.join(',')
    setSymbols(symbols)
    setPoolConfig(pool)
    setBTokenList(pool.bTokens)
    setNowToken(bToken0)
    setNowTokenId(bTokenId)
  }

  const getPoolInfo = async () => {
    if (poolConfig) {
      let res = await poolConfig.getPoolInfo(wallet, poolConfig);
      if (res) {
        let arr = bTokenList.map(item => {
          if (version === 'v2') {
            item.apy = (+(res.bTokensApy.filter(i => i.bTokenId === item.bTokenId)[0].apy) * 100)
          } else if (version !== 'v2_lite_open' && version !== 'v3' && version !== 'v3_lite') {
            item.apy = +(res.bTokensApy[0].apy) * 100
            if (isSushiLP(address)) {
              item.lpApy = res.lpApy
            }
          } else if (version === 'v3' || version === 'v3_lite') {
            let apyArr = res.bTokensApy.find(i => i.bTokenSymbol === item.bTokenSymbol)
            if (apyArr) {
              item.apy = (+apyArr.apy) * 100
              item.supplyApy = (+apyArr.supplyApy) * 100
              item.xvsApy = (+apyArr.xvsApy) * 100
            }

          }
          return item
        })
      }

      return res
    }
  }
  const getLiquidityInfo = async () => {
    let info = null
    if (poolConfig) {
      let res = await poolConfig.getPoolInfo(wallet, poolConfig);
      let apy;
      let supplyApy;
      let xvsApy;
      let liquidity;
      let apyMuList;
      let multiplier;
      if (res.bTokensApy && res.bTokensLiq) {
        if (version === 'v2' && nowTokenId) {
          apyMuList = res.bTokensApy.filter(p => p.bTokenId === nowTokenId);
          apy = apyMuList[0].apy;
          multiplier = apyMuList[0].multiplier
          liquidity = res.bTokensLiq.filter(p => p.bTokenId === nowTokenId);
          liquidity = liquidity[0].liquidity;
        } else if (version !== 'v2_lite_open' && version !== 'v3' && version !== 'v3_lite') {
          apy = res.bTokensApy[0].apy
          liquidity = res.bTokensLiq[0].liquidity
        } else if (version === 'v3' || version === 'v3_lite') {
          apyMuList = res.bTokensApy.filter(p => p.bTokenSymbol === nowToken);
          if (apyMuList.length) {
            apy = apyMuList[0].apy;
            supplyApy = apyMuList[0].supplyApy
            xvsApy = apyMuList[0].xvsApy
            multiplier = apyMuList[0].multiplier
          }
          liquidity = res.bTokensLiq.filter(p => p.bTokenSymbol === nowToken);
          if (liquidity.length) {
            liquidity = liquidity[0].liquidity;
          }
        }
        info = {
          total: liquidity,
          apy: (+apy) * 100,
          supplyApy: +(supplyApy) * 100,
          xvsApy: +(xvsApy) * 100,
          multiplier: multiplier,
        }
        
      }
      if (wallet.isConnected()) {
        let res = await poolConfig.getLiquidityInfo(wallet, address, poolConfig.version, poolConfig.isLp, type, nowToken, nowTokenId)
        info = res;
        if (version === 'v3' || version === 'v3_lite') {
          info['supplyApy'] = +(supplyApy) * 100
          info['xvsApy'] = +(xvsApy) * 100;
        }
        info['apy'] = (+apy) * 100;
        info['multiplier'] = multiplier
      }
      setPoolInfo(info)

    }
  }

  const getIsStaked = async () => {
    if (wallet.isConnected() && poolConfig) {
      let res = await poolConfig.getIsStaked(wallet, poolConfig)
      let arr = bTokenList.map((item) => {
        if (version === 'v2' && type !== 'option') {
          item.staked = res.bTokens.filter(p => p.bTokenId === item.bTokenId)[0].isStaked
        } else if (version === 'v3' || version === 'v3_lite') {
          let stakeLi = res.bTokens.filter(p => p.bTokenSymbol === item.bTokenSymbol)
          item.staked = stakeLi.length ? stakeLi[0].isStaked : false

        } else {
          item.staked = res.isStaked
        }
        return item
      })
      let obj = [];
      bTokenList.forEach((item, index) => {
        if (item.staked) {
          obj.push(item);
          bTokenList.splice(index, 1)
          return;
        }
      })
      bTokenList.unshift(...obj)
      if (bTokenList.length) {
        let bToken0 = bTokenList[0].bTokenSymbol
        if (isCakeLP(address)) {
          bToken0 = 'PANCAKE-LP'
        } else if (isSushiLP(address)) {
          bToken0 = 'DERI-USDT SLP'
        }
        let bTokenId = bTokenList[0].bTokenId
        setNowToken(bToken0)
        setNowTokenId(bTokenId)
      }

    }
  }
 

  const checkoutBaseToken = async (bTokenId, bTokenSymbol) => {
    let now;
    if (version !== 'v3' && version !== 'v3_lite') {
      now = poolConfig.bTokens.filter(b => b.bTokenId === bTokenId)
    } else {
      now = poolConfig.bTokens.filter(b => b.bTokenSymbol === bTokenSymbol)
    }
    setPoolInfo({})
    setNowTokenId(bTokenId)
    setWalletBalance('')
    setNowToken(now[0].bTokenSymbol)
    setNowIsStaked()
  }

  useEffect(() => {
    if (wallet.detail.chainId !== chainId) {
      wallet.switchNetwork(config[chainId])
    }
  }, [wallet.detail])
  useEffect(() => {
    load()
  }, [])
  useEffect(() => {
    setWalletBalance('')
    if (bTokenList.length && (nowToken || nowTokenId)) {
      let apyObj = {}
      if (version !== 'v2') {
        let apyList = bTokenList.filter(p => p.bTokenSymbol === nowToken)
        apyObj = apyList[0]
      } else if (version === 'v2') {
        let apyList = bTokenList.filter(p => p.nowTokenId === nowTokenId)
        apyObj = apyList[0]
      }
      setNowApy(apyObj)
      let now;
      if (version === 'v2' && type !== 'option') {
        if (nowTokenId) {
          now = bTokenList.filter(p => p.bTokenId === nowTokenId)[0].staked
        }
      } else if(type !== 'lp') {
        now = bTokenList.filter(p => p.bTokenSymbol === nowToken)[0].staked
      }
      setNowIsStaked(now)
    }

  }, [bTokenList, nowToken, nowTokenId])

  useEffect(() => {
    getPoolInfo()
  }, [poolConfig, bTokenList])
  
  useEffect(() => {
    getLiquidityInfo()
    getWalletBalance()
  }, [nowTokenId, poolConfig, wallet.detail.account, nowToken])
  useEffect(() => {
    getIsStaked()
  }, [poolConfig, wallet.detail.account, bTokenList])
  return (
    <div className='pool-detail'>
      <div className='back-pool'>
        <Link to={(address === '0x08aD0E0b4458183758fC5b9b6D27c372965fB780' || address === '0x66f501dda450C8978c4A1115D7b2A7FAa7702F05' || address === '0x574022307e60bE1f07da6Ec1cB8fE23d426e5831') ? '/retired' : '/pool'}>
          <div className='icon-back'>
            <img alt='' src={backIcon} />
            <span>{lang['back']}</span>
          </div>
        </Link>
      </div>
      <div className='pool-deatail-info'>
        <div className='pool-detail-basetoken-info'>
          <div className='pool-info-text'>
            <div className='pool-network-type'>
              <div className='network-logo'>
                <img alt='' src={networkLogo(chainId)} />
              </div>
              <div className='pool-type-network-text'>
                <div className='pool-type-text'>
                  {lang[poolConfig.type]} {lang[poolConfig.zone]}
                </div>
                <div className='pool-network-text'>
                  {networkText}
                </div>
              </div>
            </div>
            <div className='pool-des'>
              {type !== 'lp' && lang['amm-pool-des']}
              {version === 'clp' && <>
                {lang['deri-pancake-pool']}
              </>}
              {version === 'slp' && <>
                {lang['deri-sushi-pool']}
              </>}
              <br></br>
              {type !== 'lp' ? <a rel='norelerror' target='_blank' href='https://docs.deri.finance/library/faqs/mining-faq#is-the-amm-liquidity-mining-on-deri-pools-risk-free'><span className='learn-more'> {lang['learn-more']}</span></a> : <a rel='norelerror' target='_blank' href='https://docs.deri.finance/library/faqs/mining-faq#is-dex-liquidity-mining-on-sushiswap-sushiswap-onsen-and-pancakeswap-deri-risk-free'><span className='learn-more'> {lang['learn-more']}</span></a>}
            </div>
          </div>
          <TokenList lang={lang} address={address} checkoutBaseToken={checkoutBaseToken} nowToken={nowToken} bTokenList={bTokenList} version={version} type={type}  />
        </div>
        <div className='pool-detail-add-remove-info'>
          <div className='mining-deri-liquidity'>
            <div className='btoken-liquidity'>
              {type !== 'lp' && <>
                <div className={`btoken-logo ${nowToken}`}>
                  {version === 'v2_lite_open' && <>
                    <img alt='' onError={onImgError} src={`https://raw.githubusercontent.com/deri-finance/deri-open-zone/main/img/${nowToken}.png`} />
                  </>}
                </div>
                <div className='manage-stake'>
                  <div className='manage'>
                    {lang['manage']} {nowToken} {lang['liquidity']}
                  </div>
                  {nowIsStaked && <div className='stake'>
                    <img src={stakedLogo} alt='' />
                    {lang['staked']}
                  </div>}
                </div>
              </>}
              {type === 'lp' && bTokenList.length && <>
                <div className='lp-btoken-logo'>
                  <div className={`btoken-logo ${bTokenList[0].bTokenSymbol}`}></div>
                  <span className='conect-logo'>+</span>
                  <div className={`btoken-logo ${bTokenList[1].bTokenSymbol}`}></div>
                </div>
                <div className='manage-stake'>
                  <div className='manage'>
                    {lang['manage']} {nowToken} {lang['liquidity']}
                  </div>
                  {nowIsStaked && <div className='stake'>
                    <img src={stakedLogo} alt='' />
                    {lang['staked']}
                  </div>}
                </div>
              </>}
            </div>
            <div className='deri-current-epoch'>
              {version !== 'v2_lite_open' && <Claim poolShow={false} lang={lang} wallet={wallet} />}
            </div>
          </div>
          <div className='mining-add-remove-liquidity'>
            <PoolInfo wallet={wallet} nowApy={nowApy} poolInfo={poolInfo} chainId={chainId} address={address} nowToken={nowToken}  lang={lang} poolInfo={poolInfo} version={version} type={type} poolConfig={poolConfig} symbols={symbols} />
            <div className='add-remove-liquidity'>
              <Tab tabSet={tabSet} width={379} size={18} inactiveBgColor='#0c192d' icon='' borderBottom={true} divider={true} />
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}
export default inject('wallet', 'poolManager')(observer(Detail))