import styled from 'styled-components'
import React, { useState, useEffect } from 'react';
const Wrapper = styled.div`
    cursor: pointer;
    width:${props => props.width}px;
    height:${props => props.height}px;
    display:flex;
    .tab-left.inactive{
      border-radius:0px 0px 4px 0px;
    }
    .tab-right.inactive{
      border-radius:0px 0px 0px 4px;
    }
    .tab-left.active{
      border-radius:0px 4px 0px 0px;
      ${props => props.borderR};
    }
    .tab-right.active{
      border-radius:4px 0px 0px 0px;
      ${props => props.borderL};
    }
    .inactive,.active{
      width:50%;
      box-sizing: content-box;
      font-size:${props => props.size}px;
      display: flex;
      align-items: center;
      justify-content: center;
    }
    .inactive{
      background:${props => props.inactiveBgColor};
      ${props => props.borderB}
    }
    .active {
      color: ${props => props.activeFontColor};
      background : ${props => props.activeBgColor};
      ${props => props.borderT};
    }
    .icon-tab{
      margin-right:20px;
      width:25px;
      height:25px;
    }
  `
export default function Tab({ tabSet, inactiveBgColor, activeBgColor, icon, className, width = 238, height = 47, size = 16, borderTop = false, borderBottom = false, divider = false }) {
  let arr = Object.keys(tabSet)
  const [selected, setSelected] = useState(arr[0])
  const [styles, setStyles] = useState()
  const [borderT, setBorderT] = useState()
  const [borderL, setBorderL] = useState()
  const [borderB, setBorderB] = useState()
  const [borderR, setBorderR] = useState()
  const [activeFontColor, setActiveFontColor] = useState()
  function select(item,e) {
    // e.stopPropagation()
    setSelected(item)
  }
  useEffect(() => {
    const arr = Object.keys(tabSet)
    setSelected(arr[0])
  }, [tabSet])


  useEffect(() => {
    const arr = Object.keys(tabSet)
    let checked = selected ? selected : arr[0]
    if (tabSet[checked]) {
      setStyles(tabSet[checked].style)
      let borderT = borderTop ? `border-top:1px solid ${tabSet[checked].borderColor}` : "border-top:0"
      let borderB = borderBottom ? `border-bottom:1px solid ${tabSet[checked].borderColor}` : "border-bottom:0"
      let borderL = divider ? `border-left:1px solid ${tabSet[checked].borderColor}` : "border-left:0"
      let borderR = divider ? `border-right:1px solid ${tabSet[checked].borderColor}` : "border-right:0"
      let activeFontColor = tabSet[checked].fontColor ? tabSet[checked].fontColor : '#FFFFFF'
      setBorderT(borderT)
      setBorderL(borderL)
      setBorderB(borderB)
      setBorderR(borderR)
      setActiveFontColor(activeFontColor)
    }

  }, [tabSet, selected])

  return (
    <div style={styles} className={className}>
      <Wrapper className='tab-header' width={width} height={height}
        activeFontColor={activeFontColor} activeBgColor={activeBgColor} inactiveBgColor={inactiveBgColor} size={size} borderT={borderT} borderB={borderB} borderL={borderL} borderR={borderR}>
        {arr.map(item => (
          <div
            key={item}
            className={item === selected ? `${tabSet[item].direction} active` : `${tabSet[item].direction} inactive`}
            onClick={(e) => select(item,e)}>
            {tabSet[item].icon ? <img alt='' className='icon-tab' src={item === selected ? tabSet[item].icon : tabSet[item].inicon} /> : ""}  {item}
          </div>
        ))}
      </Wrapper>
      {tabSet[selected] ? tabSet[selected].content : ''}
    </div>
  )
}