import React, {useEffect,useState } from 'react'
import Button from "../Button/Button";
import { useAlert } from 'react-alert'
import { inject, observer } from 'mobx-react';

function TransactionButton({lang,wallet,pool = {},bTokenId,bToken,icon,label,onClick,isReady,disabled = true,statusCallback, alert,...restProps}){
  const [enabled, setEnabled] = useState(true)
  const [btnText,setBtnText] = useState(label)
  const [image, setImage] = useState(icon)

  const click = async () => {
    if(pool && wallet && wallet.detail){
      if(!wallet.isConnected()) {
        setImage(null)
        return wallet.connect()
      } else if(!wallet.isApproved) {
        setImage(null)
        let res = await wallet.approve()
        if(res){
          alert && alert.success('Approve success')
          statusCallback && statusCallback(false)
        }else{
          alert && alert.error('Approve failed')
          statusCallback && statusCallback(true)
        }
        return res;
      } else {
        setImage(icon)
        return onClick();
      }
    }
  }

  useEffect(async () => {
    if(pool && wallet && wallet.detail && pool.bTokens && lang){      
      if(!wallet.isConnected()) {
        setBtnText(lang['connect-wallet'])
        setImage(null)
      } else if(!wallet.isApproved && bToken !== 'WBNB') {
        setBtnText(lang['approve'])
        setImage(null)
        statusCallback && statusCallback(true)
      } else {
        setBtnText(label)
        setImage(icon)
        statusCallback && statusCallback(false)
      }
    }
  },[pool,wallet.detail,wallet.isApproved,lang,bToken])

  useEffect(()=>{
    setEnabled(disabled)
  },[disabled])

  useEffect(() => {
    if(wallet && pool && pool.address){
      wallet.setAddress(pool.address);
      if(pool.isV3 || pool.isV3Lite){
        wallet.setBToken(bToken)
      } else {
        wallet.setBTokenId(bTokenId)
      }
    }
  },[wallet,pool,bTokenId,bToken])
  return(
    <Button {...restProps} disabled={!enabled} label={btnText} onClick={click} icon={image}/>
  )
}
export default inject('wallet')(observer(TransactionButton))